<template>
    <main class="app-main">
        <section class="top">
            <p class="title">
                <md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>
                企业详情页
            </p>
        </section>
        <section class="middle">
            <div class="figure">
                <img :src="detail.logo" alt="">
            </div>
            <p class="article-title">基于微波光子学</p>
            <p class="collect">
                <span v-if="detail.isCollection" @click="recollect">已收藏</span>
                <span v-else @click="collect">收藏</span>
            </p>
        </section>
        <section class="bottom">
            <!--企业详情页-->
            <div v-if="detail.roleId == 2" class="title blue-line">主营产品</div>
            <div v-if="detail.roleId == 2" class="article" v-html="detail.product">
            </div>
            <div v-if="detail.roleId == 2" class="title blue-line">所属领域</div>
            <div v-if="detail.roleId == 2" class="article" v-html="detail.genre">
            </div>
            <div v-if="detail.roleId == 2" class="title blue-line">是否高新企业</div>
            <div v-if="detail.roleId == 2" class="article" v-html="detail.isNewHight === 1?'是':'否'">
            </div>
            <div v-if="detail.roleId == 2" class="title blue-line">企业法人</div>
            <div v-if="detail.roleId == 2" class="article" v-html="detail.legalPerson">
            </div>
            <div v-if="detail.roleId == 2" class="title blue-line">企业简介</div>
            <div v-if="detail.roleId == 2" class="article" v-html="detail.intro">
            </div>
            <div v-if="detail.roleId == 2" class="title blue-line">荣誉资质</div>
            <div v-if="detail.roleId == 2" class="article" v-html="detail.socialHonor">
            </div>
            <!--高校详情页-->
            <div v-if="detail.roleId == 3" class="title blue-line">优势学科</div>
            <div v-if="detail.roleId == 3" class="article" v-html="detail.advantageDisciplines">
            </div>
            <div v-if="detail.roleId == 3" class="title blue-line">高校简介</div>
            <div v-if="detail.roleId == 3" class="article" v-html="detail.intro">
            </div>
            <div v-if="detail.roleId == 3" class="title blue-line">重大平台</div>
            <div v-if="detail.roleId == 3" class="article" v-html="detail.majorPlatform">
            </div>
            <div v-if="detail.roleId == 3" class="title blue-line">典型案例</div>
            <div v-if="detail.roleId == 3" class="article" v-html="detail.classicCase">
            </div>
            <!--服务机构详情页-->
            <div v-if="detail.roleId == 4 || detail.type == 5" class="title blue-line">服务领域</div>
            <div v-if="detail.roleId == 4 || detail.type == 5" class="article" v-html="detail.genre">
            </div>
            <div v-if="detail.roleId == 4 || detail.type == 5"  class="title blue-line">机构简介</div>
            <div v-if="detail.roleId == 4 || detail.type == 5" class="article" v-html="detail.intro">
            </div>
            <div v-if="detail.roleId == 4" class="title blue-line">服务事项</div>
            <div v-if="detail.roleId == 4" class="article" v-html="detail.institutionalServices">
            </div>
            <!--其他-->
            <div v-if="detail.roleId !== 2 && detail.roleId !== 3 && detail.roleId !== 4" class="title blue-line">服务领域</div>
            <div v-if="detail.roleId !== 2 && detail.roleId !== 3 && detail.roleId !== 4" class="article" v-html="detail.genre"></div>
            <div v-if="detail.roleId !== 2 && detail.roleId !== 3 && detail.roleId !== 4" class="title blue-line">机构简介</div>
            <div v-if="detail.roleId !== 2 && detail.roleId !== 3 && detail.roleId !== 4" class="article" v-html="detail.intro"></div>
            <div class="title blue-line">附件下载</div>
            <div class="link" @click="download" v-text="detail.fileName"></div>
        </section>
        <div class="button" @click="docking">
            <!--企业-->
            <img v-if="detail.roleId == 2" width="17px" height="16px" src="@/assets/unit/business details_icon_company@2x.png" alt="">
            <span v-if="detail.roleId == 2">企业对接</span>
            <!--高校-->
            <img v-if="detail.roleId == 3" width="22px" height="17px" src="@/assets/unit/business details_icon_university@2x.png" alt="">
            <span v-if="detail.roleId == 3">高校对接</span>
            <!--机构-->
            <img v-if="detail.roleId == 4" width="20px" height="15px" src="@/assets/unit/business details_icon_agency@2x.png" alt="">
            <span v-if="detail.roleId == 4">机构对接</span>
            <!--单位-->
            <img v-if="detail.roleId == 5" width="19px" height="19px" src="@/assets/unit/business details_icon_unit@2x.png" alt="">
            <span v-if="detail.roleId == 5">单位对接</span>
        </div>
        <md-dialog
                :closable="true"
                v-model="iconDialog.open"
                :btns="iconDialog.btns"
        >
            <img src="../../../assets/technology/img@2x.png" alt="">
            <p>对接信息已发送,</p>
            <p>工作人员将会在24小时内联系您!</p>
        </md-dialog>
    </main>
</template>
<script>
import { getDetail } from '@/apiUrl/unit'
import { recollectP } from "../../../apiUrl/activity";
import { docking, collect } from '@/apiUrl/technology'
import { Icon, Toast, ActivityIndicator, Landscape, Dialog } from 'mand-mobile'
export default {
  name: 'news_detail',
  components: {
    [Icon.name]: Icon,
    [ActivityIndicator.name]: ActivityIndicator,
    [Landscape.name]: Landscape,
    [Dialog.name]: Dialog
  },
  data () {
    return {
      collected: false,
      showMaskClosable: false,
      iconDialog: {
        open: false
      },
      currentId: '',
      detail: {}
    }
  },
  mounted () {
    this.currentId = this.$route.query.id
    this.getDetail()
  },
  methods: {
    getDetail () {
      getDetail({ id: this.currentId }).then(res => {
        this.detail = res.data
      }).catch(err => {
        Toast.failed(err)
      })
    },
    // 回退
    goBack () {
      this.$router.go(-1)
    },
    docking () {
        this.$router.push({ path: '/submission/index' ,query: { type : 5 } })
      // docking({ type: 5, infoId: this.currentId }).then(res => {
      //   // this.showMaskClosable = true
      //   this.iconDialog.open = true
      // }).catch(err => {
      //   Toast.failed(err)
      // })
    },
    download () {
      window.open(this.detail.file)
    },
      // 取消收藏
      recollect () {
          const args = {
              infoId: this.currentId,
              type: 5
          }
          recollectP(args).then(res => {
              Toast.succeed('收藏成功')
              this.getDetail()
              this.collected = true
          }).catch(err => {
              Toast.failed(err)
          })
      },
    // 收藏
    collect () {
      const args = {
        infoId: this.currentId,
        type: 5
      }
      collect(args).then(res => {
        Toast.succeed('收藏成功')
        this.collected = true
        this.getDetail()
      }).catch(err => {
        Toast.failed(err)
      })
    }
  }
}
</script>

<style lang="stylus" scoped>
    .app-main
        margin-bottom 0!important
        padding-top 88px
        .middle
            padding-top 20px
            position relative
            z-index 1
            height 360px
            padding-top 40px
            .figure
                display flex
                align-items center
                margin 0 auto
                width 2rem
                height 2rem
                line-height 2rem
                border-radius 50%
                background-color #ffffff
                overflow hidden
                img
                    width 200px
            .article-title
                font-size 32px
                color #ffffff
                text-align center
                margin-bottom 10px
            .time
                width 690px
                margin 0 auto
                display flex
                justify-content left
                font-size 24px
                span
                    display flex
                    align-items center
                    color #333333
                    img
                        margin-right 10px
                .line
                    margin 0 20px
                    color #999999
                    border-left 1px solid #999999
            .collect
                width 690px
                margin 0 auto
                display flex
                justify-content center
                font-size 28px
                span
                    width 120px
                    height 36px
                    background linear-gradient(90deg,rgba(202,2,57,1),rgba(255,97,53,1))
                    border-radius 18px
                    display flex
                    justify-content center
                    align-items center
                    font-size 20px
                    color #ffffff
                    img
                        margin-right 10px
        .middle::before
                    width 100%
                    height 100%
                    content ''
                    background url("../../../assets/news/details_image_04@2x.png") no-repeat
                    position:absolute;
                    top 0
                    left 0
                    filter blur(6px)
                    z-index -1
                    background-size cover
        hr
            border none
            border-bottom 24px solid #EEEEEE
            margin 30px auto
        .bottom
            margin 40px auto 0
            padding-bottom 100px
            width 690px
            .cover
                background rgba(255, 255, 255, 1)
                box-shadow: 1px 1px 8px 0px rgba(2, 25, 85, 0.2)
                border-radius 6px
                padding 20px 50px
                margin-bottom 40px
                ul
                    display flex
                    flex-wrap wrap
                    justify-content space-between
                    li
                        font-size 28px
                        color #666666
                        flex 0 0 45%
                        text-align left
                        margin 20px auto
                        text-indent 36px
                    li:nth-child(odd)
                        background url("../../../assets/technology/item_blue@2x.png") left center no-repeat
                        background-size 26px 26px
                    li:nth-child(even)
                        background url("../../../assets/technology/item_red@2x.png") left center no-repeat
                        background-size 26px 26px
            .title
                width 690px
                height 66px
                line-height 66px
                color #333333
                font-size 28px
                text-indent 30px
                text-align left
                margin-bottom 30px
            .red-line
                background rgba(153, 153, 153, 0.1) url("../../../assets/technology/line_red@2x.png") left center no-repeat
                background-size 13px 66px
            .article
                width 690px
                padding 28px 32px
                font-size 24px
                color #666666
                line-height 36px
                text-align left
                border 2px solid rgba(153,153,153,0.3)
                border-radius 8px
                margin-bottom 0.3rem
            .blue-line
                background rgba(153, 153, 153, 0.1) url("../../../assets/technology/line_blue@2x.png") left center no-repeat
                background-size 13px 66px
            .link
                font-size 24px
                color #021955
                padding-left 18px
                text-align left
                margin-bottom 52px
        .button
            position fixed
            bottom 0
            display: flex
            align-items center
            justify-content center
            width 100%
            height 98px
            background rgba(229,232,238,1);
            font-size 32px
            box-shadow 0px 2px 12px 0px rgba(2,25,85,0.36);
            img
                margin-right 19px
    .md-landscape-content
        padding 30px
        font-size 30px
        line-height 1.5
        color #666
        p
            margin-bottom 30px
    /deep/.md-dialog-body
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        text-align center
        img
            width 80%
        p
            margin 15px auto
</style>
